.menu-block menu-btn {
  margin-right: 60px;
  margin-block-start: -60px;
  padding-left: 117px;
  display: block;
  width: 50px;
  /*height: 50px;*/
  position: absolute;
}
.menu-block menu-btn span,
.menu-block menu-btn span::before,
.menu-block menu-btn span::after {
  position: absolute;
  top: 50%; margin-top: -1px;
  left: 50%; margin-left: -10px;
  width: 20px;
  height: 2px;
  background-color: white;
}
.menu-block menu-btn span::before,
.menu-block menu-btn span::after {
  content: '';
  display: block;
  transition: 0.5s;
}
.menu-block menu-btn span::before {
  transform: translateY(-5px);
}
.menu-block menu-btn span::after {
  transform: translateY(5px);
}

.menu-block menu-btn:active span:before {
  transform: rotate(-35deg);
  width: 10px;
  transform-origin: left bottom;
}
.menu-block menu-btn:active span:after {
  transform: rotate(35deg);
  width: 10px;
  transform-origin: left top;
}

.menu-block {
  display: block;
  justify-content: center;
  align-items: center;
}

.menu-button {
  display: block;
  width: 100%;
  text-align: center;
}

